import { STYLE_MAP, TEXT_STYLE_MAP, BUTTON_STYLE_MAP, TEXT_FONTSIZE_STYLE_MAP } from './type';

export default {
  props: {
    text: {
      type: String,
      default: '确认'
    },
    size: {
      type: String,
      default: 'none'
    },
    type: {
      type: String,
      default: 'red'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    btnStyle: Object,
    textStyle: Object,
    disabledStyle: Object
  },
  computed: {
    mrBtnStyle () {
      const { type, disabled, btnStyle, size, disabledStyle } = this;

      const mrBtnStyle = {
        // ...STYLE_MAP[type],
        // ...btnStyle,
        // ...BUTTON_STYLE_MAP[size]
      };

      let disabledInStyle = { opacity: 0.2 };
      if (type === 'white') {
        disabledInStyle = { backgroundColor: 'rgba(0, 0, 0, 0.1)' };
      }

      return disabled ? {
        // ...mrBtnStyle,
        // ...disabledInStyle,
        // ...disabledStyle,
        borderWidth: 0
      } : mrBtnStyle;
    },
    mrTextStyle () {
      const { type, disabled, textStyle, size } = this;
      const mrTextStyle = { 
        // ...TEXT_STYLE_MAP[type],
        // ...textStyle, 
        // ...TEXT_FONTSIZE_STYLE_MAP[size]
         };
      return disabled ? {  color: '#FFFFFF' } : mrTextStyle;
    }
  },
  methods: {
    onClicked (e) {
      const { type, disabled } = this;
      this.$emit('wxcButtonClicked', { e, type, disabled });
    }
  }
};
